@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact{
    .section_content{
        .title{
            font-size: 7rem;
            margin: 10rem 0;

            @include m.tablet {
                font-size: 12rem;
            }
        }
        .section_div{
            p{
                text-align: center;
                margin-top: 0;
                margin-bottom: 3rem;
        
                @include m.tablet {
                    width: 98%;
                    margin: 0 auto;
                    margin-bottom: 3rem;
                }
            }
        }
    }
}