@use 'base/variables' as v;
@use 'base/mixins' as m;

.us{

    p{
        color: v.$lightGrey;
    }
    padding: 1rem;
    h1{
        text-align: center;
        margin: 0 0 10rem;
        padding-top: 15rem;
        color: v.$white;
        @include m.tablet {
            margin: 0 0 15rem;
            padding-top: 20rem;
        }
    }

    h2{
        color: v.$greyBlue;
        text-transform: uppercase;
        text-align: center;
        margin-top: 20rem;
        @include m.tablet {
            text-align: start;
            padding: 0 2rem 0;
            font-size: 5rem;
        }
    }
    .business{
        padding: 0rem;
        img{
            max-width: 100%;

            @include m.tablet {
                display: block;
                margin: 0 auto;
                max-width: 70%;
            }
        }

        .us_text{
            p{
                font-family: v.$font_variant;
                line-height: 1.6;
                font-size: 1.6rem;

                @include m.tablet {
                    font-size: 3rem;
                    padding: 1rem 5rem;
                }
            }
        }
    }
    .team{
        padding: 0;
        @include m.tablet {
            padding: 2rem;
            display: flex;
            justify-content: space-between;
            gap: 2rem;
        }

        .teamImage{
            background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(32, 32, 32, 0.6)),
            url(../../build/img/yo.png);
            background-size: cover;
            background-position: center center;
            height: 50vh;
            max-width: 100%;
            margin: 0;
            border-radius: .5rem;
            @include m.tablet {
                flex: 0 0 55%;
                background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(32, 32, 32, 0.6)),
                url(../../build/img/yo.png);
                background-size: cover;
                background-position: center center;
                height: 70vh;
                max-width: 100%;
                align-content: start;
            }
            h3{
                font-size: 2.1rem;
                padding: 2rem 1rem;
                color: lighten(v.$grey, 20%);
                margin: 0;
                
                @include m.tablet {
                    text-transform: uppercase;
                    font-size: 2.6rem;
                }
            }
        }
        .us_text{
            p{
                font-family: v.$font_variant;
                line-height: 1.6;
                font-size: 1.6rem;

                @include m.tablet {
                    font-size: 2rem;
                }

            }
        }
    }
}